.mozok {
  text-align: center;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  background-color: grey;
}
.header {
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: block;
  background-color: white;
  min-height: 12dvh;
  max-height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.title {
  font-size: 2rem;
  font-weight: 800;
  padding: 10px;
}
.line {
  width: 80vw;
  height: 2px;
  background-color: black;
  margin-left: 10vw;
}

.Link {
  display: inline-flex;
  list-style: none;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  flex-grow: 1;
}
.Link:hover {
  background-color: rgb(180, 180, 180);
  color: #fff;
}
.Menu {
  display: flex;
  font-weight: normal;
}
.topContainerOne {
  margin-left: 10vw;
  width: 80vw;
  display: flex;
}
.topContainerTwo {
  margin-left: 10vw;
  width: 80vw;
  min-height: 10px;
  background-color: rgb(255, 255, 255);
}
.catalog {
  display: flex;
  width: 22dvh;
  background-color: rgb(20, 126, 197);
  color: white;
  padding: 10px;
  margin: 5px;
}
.catalogText {
  margin-left: 1dvw;
  font-size: 1.1rem;
  font-weight: 800;
}
.catalog:hover,
.headerBtn:hover {
  cursor: pointer;
  background-color: rgba(20, 126, 197, 0.61);
}

.Link:visited {
  color: #0e1333;
}
